<template>
<div id="myApplication">
    <h1>Hello World</h1>
    <p>little text behind the title</p>
    <p>{{ info }}</p>
    </div>
</template>

<script>
    
    import axios from 'axios';
    export default {
        
        data(){
            return {
                info: "this is a prop test!!"
            }
        },
        created:function () {
            axios.get('http://haste.test/cms/wp-json')
                .then(response => (this.info = response.data))
                .catch(function (error) {
                console.log(error);
            });
        },
    }
</script>

<style>
    #app {
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }
</style>


